












































































































	.with-background{
		background-color: #D5D9DF;
		background-position: center top,left top;
		background-size: auto;
		padding: 20px;
		opacity: 0.95;
		&.upside-down{
			background-position: bottom;
		}
	}
	body{
		font-family: "Baskerville";
		font-size: 1.5rem;
	}
	header{
		background-position: center top,left top;
		background-size: cover;
		height: 15vh;
	}
	.title {
		font-family: "Adora Bouton";
		font-size: 3rem;
	}
	.navigation{
		display: flex;
		gap: 3vw;
		justify-content: center;
	}
	.main-picture{
		display: block;
		width: 80%;
		margin-bottom: 30px;
		margin-left: 10%;
		margin-top: 25px;
		box-sizing: border-box;
		border: solid 8px #fff;
		box-shadow: 0 1px 4px rgba(0,0,0,0.25);
	}
	.leaf{
		width: 100px;
		height: 25px;
		&.left{
			transform: rotateY(180deg);
		}
    }
	.poem{
		font-size: 1.25rem;
		text-align: center;
		white-space: pre-line;
	}
	.sub-title {
		font-size: 2rem;
		font-weight: 600;
	}
	.leafs-container{
		align-items: center;
		display: flex;
		gap: 10px;
		justify-content: center;
	}
	.wedding-date{
		font-weight: 900;
		font-size: 35px;
	}
	.menetrend-lista{
		font-size: 1.42rem;
		text-align: start;
		max-width: 350px;
		margin: auto;

		strong{
			padding-right: 10px;
		}
	}
	li{
		padding: 5px 0px;
	}
